.shopping-list-header {
  color: #111;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
}

body.dark-theme .shopping-list-header {
  color: #fff;
}

.empty-shopping-list-div {
  padding: 80px 55px;
}

.empty-shopping-list-msg {
  text-align: center;
  color: rgba(0, 0, 0, .6);
  margin-bottom: 14px;
}

.empty-shopping-list-img {
  width: 265px;
  height: 198px;
}

.title-color {
  color: #4f2ee8;
}

.support-ukraine {
  display: none;
}

.shopping-book-list {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.shoping-book-card {
  width: 335px;
  height: 170px;
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  gap: 14px;
  padding: 14px;
  display: flex;
}

.shoping-book-card-img > img {
  width: 100px;
  height: 142px;
}

.shoping-book-card-text {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.shoping-book-card-ganre {
  color: #b4afaf;
  padding-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
}

.shoping-book-card-title_and_ganre {
  flex-direction: column;
  gap: 4px;
  display: flex;
}

.shoping-book-card-title {
  color: #111;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  font-size: 16px;
  font-weight: 700;
  display: -webkit-box;
  overflow: hidden;
}

.shoping-book-card-description {
  color: #111;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-size: 14px;
  font-weight: 400;
  display: -webkit-box;
  overflow: hidden;
}

.shoping-book-card-author {
  color: #b4afaf;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
}

@media screen and (min-width: 768px) {
  .shopping-list-header {
    font-size: 48px;
  }

  .shopping-list.section {
    padding-top: 0;
  }

  .empty-shopping-list-div {
    width: 437px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
  }

  .empty-shopping-list-msg {
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 18px;
  }

  .empty-shopping-list-img {
    width: 322px;
    height: 241px;
  }

  .shoping-book-card {
    width: 704px;
    height: 213px;
    gap: 24px;
    padding: 24px;
  }

  .shoping-book-card-img > img {
    width: 116px;
    height: 165px;
  }

  .shoping-book-card-ganre {
    padding-bottom: 10px;
  }

  @media screen and (min-width: 1440px) {
    .shopping-list-div {
      width: 996px;
    }

    .container-shop {
      width: 1440px;
      justify-content: left;
      align-items: start;
      margin: 0 auto;
      padding: 0 24px;
      display: flex;
    }

    .shop-list-support-ukraine > section.support-ukraine {
      margin: 0;
      display: block;
    }

    div.shopping-list-div {
      padding-left: 40px;
    }

    .book-card-description {
      width: 808px;
    }

    .shopping-list-header {
      margin-top: 0;
    }

    .container-shop {
      margin-top: 40px;
    }

    .shoping-book-card {
      width: 996px;
    }

    .shoping-book-card-img > img {
      width: 116px;
      height: 165px;
    }

    .shoping-book-card-ganre {
      padding-bottom: 10px;
    }
  }
}

